<div class="container"><div class="form-group linkBack"><router-link to="/"><img class="me-2" src="@/assets/images/back.svg">Главная</router-link></div><div class="row"><div class="col-12 d-flex justify-content-center"><div class="loadMain"><h3>Спарсить сайт</h3><p>Получить данные с сайта в виде таблицы</p><label class="form-label" for="linkPars">Ссылка на сайт (url)</label><input class="form-control" id="linkPars" type="text" v-model="link" placeholder="https://example.com/"><button class="btnSecondary w-100 mt-3" @click="$_vision_load_toParseSite">Отправить</button></div></div><div class="d-flex justify-content-center mt-5" v-if="spinner"><div class="spinner-border text-light"></div></div><div v-if="result.load"><h3 class="mb-3 mt-5">Полученный результат</h3><div class="loadResultParse"><div class="form-group"><h3>Полученное описание</h3><p>{{result.html}}</p></div><div class="form-group"><h3>Определённые отрасли</h3><p><ul><li v-for="item in result.industries">ID: {{item.id}} --- {{item.name}}</li></ul></p></div><div class="form-group"><h3>Определённые технологии</h3><p><ul><li v-for="item in result.tehnology">ID: {{item.id}} --- {{item.name}}</li></ul></p></div></div></div></div></div>